export const editRosok = (
  kodeJenis,
  statusAktif,
  kodeKategori,
  namaJenis,
  kadarBeli,
  kadarJual,
  kadarCetak
) => {
  return (dispatch) => {
    dispatch({
      type: EDIT_KODE_BARANG,
      payload: {
        kodeJenis: kodeJenis,
        statusAktif: statusAktif,
        kodeKategori: kodeKategori,
        namaJenis: namaJenis,
        kadarBeli: kadarBeli,
        kadarJual: kadarJual,
        kadarCetak: kadarCetak,
      },
    });
  };
};
