import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const PrintNotaSuratJalan = (
  row1name = "",
  row1isi = "",
  row2name = "",
  row2isi = "",
  row3name = "",
  row3isi = "",
  row4name = "",
  row4isi = "",
  username = "",
  tanggal = "",
  validby = "",
  tableColumn = [],
  title = "",
  tableRows,
  gambar = [],
  judul = "SURAT JALAN"
) => {
  // initialize jsPDF
  const doc = new jsPDF('portrait');
  doc.setProperties({
    title: title,
  });
  const logo =
    "data:image/png;base64,";

  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: 47,
    theme: "plain",
    rowPageBreak: "avoid",
    pageBreak: "avoid",
    margin: { top: 20 },
    bodyStyles: { lineColor: "#000",fontSize : 8, },
    headStyles: {
      lineColor: "#000",fontSize : 8,
      fillColor: [212, 212, 211],
    },
  });
  let finalY = doc.lastAutoTable.finalY + 7;
  let finalX = 14;
  let spliter = 0;
  if (row3isi === "ADMIN INTERN") {
    gambar.map((data) => {
      console.log(data);
      if (spliter % 5 === 0) {
        if (spliter === 0) {
          finalX = 14;
        } else {
          finalY = finalY + 30;
          finalX = 14;
        }
      }
      doc.addImage(data?.lokasi_gambar, "PNG", finalX, finalY, 60, 40);
      spliter = spliter + 1;
      finalX = finalX + 50;
      return true;
    });
  }

  doc.setFontSize(17);
  doc.text(judul, 14, 33);
  doc.setFontSize(10);
  doc.addImage(logo, "PNG", 14, 15, 35, 12);
  //row 1
  doc.text(row1name, 14, 40);
  doc.text(row1isi === "" ? "" : ":", 37, 40);
  doc.text(row1isi, 40, 40);
  doc.text(row2name, 120, 40);
  doc.text(row2isi === "" ? "" : ":", 141, 40);
  doc.text(row2isi, 145, 40);
  //row 2
  doc.text(row3name, 14, 45);
  doc.text(row3isi === "" ? "" : ":", 37, 45);
  doc.text(row3isi, 40, 45);
  doc.text(row4name, 120, 45);
  doc.text(row4isi === "" ? "" : ":", 141, 45);
  doc.text(row4isi, 145, 45);

  //Stamp
  doc.text("User", 240, finalY + 35);
  doc.text(":", 248, finalY + 35);
  doc.text(username, 250, finalY + 35);
  doc.text("Cetak", 240, finalY + 40);
  doc.text(":", 248, finalY + 40);
  doc.text(tanggal, 250, finalY + 40);
  doc.text("Valid", 240, finalY + 45);
  doc.text(":", 248, finalY + 45);
  doc.text(validby, 250, finalY + 45);

  doc.text("Penerima", 25, finalY + 60);
  doc.text("Pengirim", 95, finalY + 60);
  doc.text("Mengetahui", 160, finalY + 60);
  // doc.autoPrint();
  // doc.save(`${title}_${dateStr}.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>" +
      title +
      "</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  x.document.close();
};

export default PrintNotaSuratJalan;
