import { AxiosMasterGet } from "../Axios";
import { checkLaporan } from "../helper";

export const GET_LIST_SELESAI_REPARASI =
  "GET_LIST_SELESAI_REPARASI";

export const getSelesaiReparasi = () => {
  return (dispatch) => {
    AxiosMasterGet(`reparasi/get/belum-selesai`)
      .then((res) => {
        if (checkLaporan(res.data)) {
          dispatch({
            type: GET_LIST_SELESAI_REPARASI,
            payload: {
              data: res.data,
              alert:
                "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
              error: false,
            },
          });
        } else {
          dispatch({
            type: GET_LIST_SELESAI_REPARASI,
            payload: {
              data: res.data,
              alert:
                "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
              error: false,
            },
          });
        }
      })
      .catch(() =>
        dispatch({
          type: GET_LIST_SELESAI_REPARASI,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

