export const setCustomer = (
  level1,
  level2,
  level3,
  nama1,
  nama2,
  nama3,
  levelCustomer
) => {
  return (dispatch) => {
    dispatch({
      type: SET_CUSTOMER_SELECTED,
      payload: {
        level1: level1,
        nama1: nama1,
        nama2: nama2,
        nama3: nama3,
        level2: level2,
        level3: level3,
        levelCustomer: levelCustomer,
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
        error: false,
      },
    });
  };
};
