export const calculate_level2 = ({ data, hasil, res }) => {
  var simpan_level2 = {
    netto_disc:
      (parseFloat(data.bruto || 0) * parseFloat(res.data.level2)) / 100 -
      (parseFloat(data.discGlobalPr) * parseFloat(data.bruto || 0)) / 100,
    gross: parseFloat(data.bruto || 0) + parseFloat(data.pkg || 0), //berat + bungkus
    netto:
      (parseFloat(data.bruto || 0) * parseFloat(res.data.level2)) / 100 || 0, //murni
    qty: data.qty || 1,
    k: parseFloat(data.k || 0),
    ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
    kadar_cetak: (hasil[0].kadar_cetak || 0).toString(),
    disc_persen: data.discGlobalPr,
    kadar_jual: parseFloat(data.kadarTransaksi || 0), //harga%
    kadar_level2: res.data.level2,
    kadar_level3: res.data.level3,
    kadar_modal: hasil[0].kadar_beli, //harga%
    kode_jenis: hasil[0].kode_jenis, //kodebarang
    kode_barcode: "-",
    kode_barcode_set: data.kode_barcode_set || "-",
    p: parseFloat(data.p || 0),
    disc: (parseFloat(data.discGlobalPr) / 100) * parseFloat(data.bruto || 0),
    pkg: parseFloat(data.pkg || 0),
    bruto: parseFloat(data.bruto || 0), //berat
    harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
  };
  return simpan_level2;
};

