export const NormalizeGramasi = (value, previousValue) => {
  if (value) {
    return value.replace(/[^0-9.]+/g, "");
  } else {
    return value;
  }
};
export const NormalizeKadar = (value, previousValue) => {
  if (value) {
    return value.replace(/[^0-9.KARTkart]+/g, "");
  } else {
    return value;
  }
};

export const NormalizeNumberOnly = (value, previousValue) => {
  if (value) {
    return value.replace(/[^\d]/g, "");
  } else {
    return value;
  }
};

export const resizeFile = (file) =>
  new Promise((resolve, reject) => {
    Resizer.imageFileResizer(
      file,
      480,
      720,
      "PNG",
      30,
      0,
      (uri) => {
        resolve(uri);
      },
      "base64"
    );
  });

export const dataURLtoFile = (dataurl, filename) => {
  // export const dataURLtoFile = (dataurl, filename) =>
  let arr = dataurl.split(","),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], filename, { type: mime });
};

