import { AxiosMasterGet } from "../Axios";
import { checkLaporan } from "../helper";

export const GET_LIST_VALIDASI_REPARASI =
  "GET_LIST_VALIDASI_REPARASI";
export const GET_LIST_VALIDASI_REPARASI_SELECTED =
  "GET_LIST_VALIDASI_REPARASI_SELECTED";

export const getValidasiReparasi = () => {
  return (dispatch) => {
    AxiosMasterGet(`reparasi/get/UnValidasiAll`)
      .then((res) => {
        if (checkLaporan(res.data)) {
          dispatch({
            type: GET_LIST_VALIDASI_REPARASI,
            payload: {
              data: res.data,
              alert:
                "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
              error: false,
            },
          });
        } else {
          dispatch({
            type: GET_LIST_VALIDASI_REPARASI,
            payload: {
              data: res.data,
              alert:
                "alert alert-danger alert-dismissible bg-danger text-white border-0 fade d-none",
              error: false,
            },
          });
        }
      })
      .catch(() =>
        dispatch({
          type: GET_LIST_VALIDASI_REPARASI,
          payload: {
            data: false,
            alert:
              "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
            error: true,
          },
        })
      );
  };
};

export const getValidasiReparasiSelected = (row) => {
    var hasil = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
  return (dispatch) => {
    dispatch({
      type: GET_LIST_VALIDASI_REPARASI_SELECTED,
      payload: {
        data: hasil,
        alert:
          "alert alert-danger alert-dismissible bg-danger text-white border-0 fade show",
        error: true,
      },
    });
  };
};

